:export{mediumScreen:992px;smallScreen:540px}.product-list-item{text-align:center}.product-list-item__title{font-weight:400;font-size:12px;margin:1rem 0 0 0;color:#000;text-align:center;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}@media (max-width: 575px){.product-list-item__title{margin:1rem 0 0}}@media (max-width: 540px){.product-list-item__title{font-size:12px;font-weight:300;line-height:normal;height:30px}}.product-list-item__undiscounted_price{text-decoration:line-through;color:#c4c4c4;font-size:.875rem;font-weight:regular}.product-list-item__category{color:#c4c4c4;text-align:left}.product-list-item__price{font-weight:600;margin:1rem 0 0;text-align:left}.product-list-item__image{background-color:#f1f5f5;display:flex;height:100px;justify-content:center;border-radius:5px;overflow:hidden;border:1px solid #f4f4f4;box-shadow:0 2px 10px 0 rgba(0,0,0,0.1)}.product-list-item__image img{width:100%}@media (max-width: 540px){.product-list-item__image{height:58px}}
