:export{mediumScreen:992px;smallScreen:540px}.carousel__control{padding:0.2rem 0.5rem;background-color:#fff;border-radius:25px;box-shadow:0px 0px 10px 0px rgba(0,0,0,0.25)}.carousel__control:hover{cursor:pointer}.carousel__control--right{margin-right:0}.carousel__control--right svg{transform:rotate(180deg);margin-top:9px;width:15px;margin-left:8px}.carousel__control--right svg path{fill:#f34928}.carousel__control--left{margin-left:0}.carousel__control--left svg{margin-top:6px;width:15px;margin-right:8px}.carousel__control--left svg path{fill:#f34928}@media (max-width: 540px){.carousel__control{padding:0;background-color:transparent;border-radius:0;box-shadow:none}}.carousel .slider-slide{text-align:center}
