:export{mediumScreen:992px;smallScreen:540px}.side-nav{width:30%;background-color:#fafafa;transition:0.5s linear;right:0 !important;position:absolute}.side-nav .menu-list{min-height:100vh;padding:0px 25px 0px 10px}.side-nav .regBtn{background-color:#092540 !important;padding:1rem !important;border-radius:4px;text-transform:none !important;display:flex;margin:1rem 0;justify-content:center;align-items:center;position:relative;height:40px;width:100%}.side-nav .regBtn span{display:block;text-align:center;transform:none;text-transform:capitalize;width:100%;font-size:15px !important}.side-nav .bcBtn{width:100%;border-radius:4px;background:#fff;border:1px solid #cccccc78;box-shadow:none !important;margin:0 0 1rem}.side-nav .bcBtn span{color:#414141;text-transform:capitalize;font-weight:400}.side-nav .loBtn{min-width:260px;display:block;margin:0 auto 1rem;position:absolute;bottom:0;left:1rem;right:1rem}.side-nav .loBtn button{width:100%;border-radius:4px;background:#fff;border:1px solid #cccccc78;box-shadow:none !important}.side-nav .loBtn button span{color:#e53e3b;text-transform:capitalize;font-weight:400}.side-nav__menu-item{border-bottom:1px solid #f6f6f6;display:grid;height:3.55rem}.side-nav__menu-item-link{align-items:center;width:100%;border-radius:4px;background:#f74b2c;color:#fff;display:flex;text-align:center;justify-content:center;font-weight:400;padding:0.9rem 1rem;text-decoration:none;text-transform:capitalize;transition:0.3s;margin:0 0 1rem}.side-nav__menu-item-back span{align-items:center;color:#323232;display:flex;font-weight:600;padding:1rem;text-decoration:none;text-transform:capitalize;transition:0.3s}.side-nav__menu-item-back span svg *{fill:currentColor}.side-nav__menu-item-logo{align-self:center;padding:0 1rem}.side-nav__menu-item-logo img{margin:-3px 0 0}.side-nav__menu-item-link,.side-nav__menu-item-more,.side-nav__menu-item-back,.side-nav__menu-item-close{transition:0.3s}.side-nav__menu-item-link:hover,.side-nav__menu-item-more:hover,.side-nav__menu-item-back:hover,.side-nav__menu-item-close:hover{color:#51e9d2}.side-nav__menu-item-link svg *,.side-nav__menu-item-more svg *,.side-nav__menu-item-back svg *,.side-nav__menu-item-close svg *{fill:currentColor}.side-nav__menu-item-close:hover,.side-nav__menu-item-more:hover,.side-nav__menu-item-back:hover{cursor:pointer}.side-nav__menu-item-more{align-self:center;padding:1.25rem 1.75rem}.side-nav__menu-item-back svg{margin-right:1rem}.side-nav__menu-item-close{align-items:center;display:flex;height:3.55rem;justify-content:center;position:relative}.side-nav__menu-item-close .lineOne{transform:rotate(45deg)}.side-nav__menu-item-close .lineTwo{transform:rotate(-45deg)}.side-nav__menu-item-close span{background-color:#ff4807;border-radius:1px;height:3px;width:24px;position:absolute}.side-nav__menu-item-close:hover{color:#51e9d2}.side-nav__menu-item--has-subnavigation,.side-nav__menu-item--parent{grid-template-columns:1fr 3.55rem}.side-nav__menu-item--parent{color:#c4c4c4;background:#fff}.side-nav__menu-item--parent a{color:inherit}@media (max-width: 768px){.side-nav{width:100%}}.side-nav .menu-list{min-height:100vh;padding:0px 10px 0px 10px}
