:export{mediumScreen:992px;smallScreen:540px}.login{width:25rem;max-width:100vw}.login__tabs{display:flex}.login__tabs span{flex-grow:1;width:100%;background-color:#f6f6f6;text-transform:uppercase;font-weight:600;text-align:center;padding:1rem;border-bottom:1px solid #f6f6f6;cursor:pointer}.login__tabs span:first-of-type{border-right:1px solid #fff}.login__tabs span:last-of-type{border-left:1px solid #fff}.login__tabs span.active-tab{border-bottom:1px solid #51e9d2}.login__content{padding:1rem}.login__content .input{margin-bottom:1rem}.login__content__button{margin:2rem 0;text-align:center}.login__content__password-reminder{color:#c4c4c4;font-size:.875rem;margin-top:2rem}.login .errorMessages{color:red}.login .submitBtn{width:100%;border-radius:4px;background:#f74b2c;color:#fff;text-transform:capitalize;box-shadow:none !important}.login .submitBtn span{color:#fff;text-transform:capitalize}.login .submitBtn:hover{background:#f74b2c}
