:export{mediumScreen:992px;smallScreen:540px}.s-products-featured{padding:0 0 2rem}.s-products-featured h3{font-weight:400;margin:2.6rem 0 3rem;font-size:32px;color:#000}.s-products-featured .pro-list{display:flex;justify-content:space-between;align-items:flex-start;flex-wrap:wrap}.s-products-featured .modalDiv{color:#323232;display:inline-block;text-decoration:none;width:39%;cursor:pointer}.s-products-featured .modalDiv:nth-child(even){margin:0 0 3rem 0 !important}.s-products-featured .modalDiv:nth-child(odd){margin:0 1rem 3rem 0 !important}@media (max-width: 768px){.s-products-featured .modalDiv{width:100%}}@media (max-width: 540px){.s-products-featured .modalDiv{width:20%}.s-products-featured .modalDiv:nth-child(even){margin:unset !important}.s-products-featured .modalDiv:nth-child(odd){margin:unset !important}.s-products-featured .modalDiv:nth-child(4){margin:0 0 1rem 0 !important}.s-products-featured h3{margin-top:0;margin-bottom:1rem;font-size:18px}}
