:export{mediumScreen:992px;smallScreen:540px}.social-icon{padding:1rem .5rem}.social-icon svg *{transition:0.3s}a:first-child .social-icon{padding:1rem 1rem 1rem 0rem}.social-icon svg{-webkit-transition:all 0.3s ease-in-out;-moz-transition:all 0.3s ease-in-out;-ms-transition:all 0.3s ease-in-out;-o-transition:all 0.3s ease-in-out;transition:all 0.3s ease-in-out}.social-icon svg:hover,.social-icon svg:active{-webkit-transform:scale(1.1);-moz-transform:scale(1.1);-ms-transform:scale(1.1);-o-transform:scale(1.1);transform:scale(1.1)}
